.content {
	position: absolute;
	bottom: 10px;
	right: 20px;
}

::-webkit-scrollbar {
	display: none;
}

.content > * {
	display: flex;
	justify-content: flex-end;
	align-items: flex-end;
}

.content > div > a {
	font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
		"Lucida Sans Unicode", Geneva, Verdana, sans-serif;
	text-decoration: none;
	color: black;
	padding-bottom: 35px;
	text-transform: uppercase;
	font-size: 1.5rem;
	font-weight: 200;
	letter-spacing: 1px;
}

@media screen and (min-width: 670px) {
	.content > div > a {
		font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
			"Lucida Sans Unicode", Geneva, Verdana, sans-serif;
		text-decoration: none;
		color: black;
		padding-bottom: 70px;
		text-transform: uppercase;
		font-size: 3.5rem;
		font-weight: 200;
		letter-spacing: 2px;
	}
	.content {
		position: absolute;
		left: 200px;
		top: 15px;
		bottom: 20px;
		right: 20px;
		overflow: scroll;
		margin: 0;
		padding: 0;
		-webkit-overflow-scrolling: touch;
	}
}
